<script>
import { mapActions, mapGetters } from "vuex";
import { repertory } from "common/utils/public_function/commonUtils";
export default {
  name: "instorageThreeoutstorageThreeoutstorageSee",
  data() {
    return {
      authority: {},
      sum: null,
      tableData: [],
      totalPrice: 0,
      params: {},
      fromSee: true,
    };
  },
  methods: {
    ...mapActions(["threeoutstorageIndexACT"]),
    goBack() {
      if (!this.fromSee) {
        this.$router.push({ path: "/sales/customrebatedetail" });
      } else {
        this.threeoutstorageIndexACT({
          goPage: this.threeoutstorageSeeGet.goPage,
          params: this.threeoutstorageSeeGet.params,
          pageSize: this.threeoutstorageSeeGet.pageSize,
        });
        this.$router.push({
          path: "/inventory/threeoutstorage",
        });
      }
    },
    getSummaries(param) {
      // 合并计算
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "合计";
          return;
        }
        // console.log(column.property)
        if (column.property === "settlement_price") {
          const values = data.map((item) => Number(item[column.property] || 0));
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            return Number(prev + curr);
          }, 0);
          this.totalPrice = sums[index];
          sums[index] = Number(sums[index])
            .toFixed(2)
            .toString()
            .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          this.countSettlementPrice();
        }
        if (column.property === "total_price") {
          const values = data.map((item) => Number(item[column.property] || 0));
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            return Number(prev + curr);
          }, 0);
          sums[index] = Number(sums[index])
            .toFixed(2)
            .toString()
            .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
        }
      });
      return sums;
    },
    countSettlementPrice() {
      // 本单使用返利+本单使用授信+本单结算金额=订单的合计金额；
      let valArry = ["rebate_price", "credit_price", "deposits_price"];
      valArry.forEach((value) => {
        this.$set(
          this.params,
          value,
          !this.params[value] || this.params[value] < 0 ? 0 : this.params[value]
        );
      });
      let settlement_price =
        Number(this.totalPrice) -
        Number(this.params.rebate_price) -
        Number(this.params.credit_price) -
        Number(this.params.deposits_price);
      this.$set(this.params, "settlement_price", settlement_price.toFixed(2));
    },
    auditOrUnaudit(api) {
      //审核
      let vm = this;
      if (api == "auidtorPassThreeOutStorage") {
        // 校验对接车郎中
        this.$baseData
          .checkCarDoctor({
            bill_type: 13,
            bill_id: vm.params.outstock_id,
            operation_type: 4,
          })
          .then(() => {
            // 校验安全库存金额
            this.$axMethod
              .safetyStockCheck({
                bill_logogram: "tbck",
                order_type: 2,
                bill_id: vm.params.outstock_id,
              })
              .then(() => {
                repertory(
                  this,
                  {
                    goods_info: vm.params.outstock_id,
                    status: 2,
                    type: 1,
                  },
                  function () {
                    vm.$axMethod[api]({
                      outstock_id: vm.params.outstock_id,
                    }).then(({ data }) => {
                      if (data.code == 200) {
                        vm.$message({
                          message: data.msg || data.data,
                          type: "success",
                        });
                        vm.goBack();
                      }
                    });
                  }
                );
              });
          });
      } else {
        // 校验对接车郎中
        this.$baseData
          .checkCarDoctor({
            bill_type: 13,
            bill_id: this.params.outstock_id,
            operation_type: 6,
          })
          .then(() => {
            this.$axMethod[api]({
              outstock_id: this.params.outstock_id,
            }).then(({ data }) => {
              if (data.code == 200) {
                this.$message({
                  message: data.msg || data.data,
                  type: "success",
                });
                this.goBack();
              }
            });
          });
      }
    },
  },
  computed: {
    ...mapGetters(["threeoutstorageSeeGet"]),
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));

    if (this.$route.query.id || this.$route.query.outstock_sn) {
      this.$axios({
        method: "get",
        url: this.$api.infoThreeOutStorage,
        params: {
          outstock_id: this.$route.query.id,
          outstock_sn: this.$route.query.outstock_sn
        },
      }).then((res) => {
        if (res.data.code == 200) {
          this.params = res.data.data;
          this.tableData = res.data.data.goods_info;
        }
      });
    }
  },
};
</script>


